@import "_settings";

body {
	counter-reset: sidenote-counter;
}

article.tufte {
	// width: 87.5%;
    margin-left: auto;
    margin-right: auto;
    // padding-left: 12.5%;
    // font-family: et-bembo, Palatino, "Palatino Linotype", "Palatino LT STD", "Book Antiqua", Georgia, serif;
    background-color: $bg-color;
    color: $text-color;
    max-width: 1400px;
}

.tufte {
	p, footer, div.table-wrapper, div.mathblock, blockquote { width: 65%; }
	blockquote { p, li { width: 100% } }
	// li .sidenote, li .marginnote{ margin-right: -80%; } //added to allow for the fact that lists are indented and marginnotes and sidenotes push to right
	// blockquote .sidenote, blockquote .marginnote { margin-right: -79% }
}

.tufte blockquote blockquote {
	width: 85%;
}

.fullwidth {
	p, footer, div.table-wrapper, div.mathblock, blockquote, li { width: 100%; }
}

:not(article.tufte) { .sidenote, .sidenote-number, .marginnote { display: none; } }

figure.fullwidth figcaption { float: left; margin-right: 0%; margin-left: 36%; }

fullwidth, li.listing div{ max-width: 90%; }



img { max-width: 100%; }

.sidenote, .marginnote {
	float: right;
	clear: right;
	margin-right: -55%;
	width: 50%;
	margin-top: 0;
	margin-bottom: 1.96rem;
	font-size: 88%;
	line-height: 1.2; // 1.96; //changed to bring line heights into rational pattern
	vertical-align: baseline;
	position: relative;
}

.sidenote-number { counter-increment: sidenote-counter; }

.sidenote-number:after, .sidenote:before {
	content: counter(sidenote-counter) " ";
	color: #333333;
	text-decoration: none;
    //font-family: et-bembo-roman-old-style;
   	position: relative;
   	vertical-align: baseline;
    font-weight: 700;
 }

.sidenote-number:after { content: counter(sidenote-counter);
                         font-size: 70%;
                         font-weight: 700;
                         top: -0.5rem;
                         left: 0.1rem; }

.sidenote:before { content: counter(sidenote-counter) ".\000a0"; // this is unicode for a non-breaking space
                   top: 0rem; } //removed superscripting for numerical reference in sidenote

div.table-wrapper { overflow-x: auto; } //changed all overflow values to 'auto' so scroll bars appear only as needed

@media screen and (max-width: 760px) {
	.tufte {
	   p, footer,div.mathblock { width: 90%; }
	   pre code { width: 87.5%; }
	   ul { width: 85%; }
	   figure { max-width: 90%; }
	   figcaption, figure.fullwidth figcaption { margin-right: 0%;
	                                             max-width: none; }
	   blockquote p, blockquote footer {  width: 80%;
	                                      padding-left: 5%;
	                                      padding-right: 5%;
	                                    }
	}
}

.marginnote code, .sidenote code { font-size: 1rem; } //more .code class removal


input.margin-toggle { display: none; }

label.sidenote-number { display: inline; }

label.margin-toggle:not(.sidenote-number) { display: none; }

@media (max-width: 760px) {
	.tufte {
		label.margin-toggle:not(.sidenote-number) { display: inline; color: $contrast-color; }
	    .sidenote, .marginnote { display: none; }
	    .margin-toggle:checked + .sidenote,
	    .margin-toggle:checked + .marginnote { display: block;
	                                           float: left;
	                                           left: 1rem;
	                                           clear: both;
	                                           width: 95%;
	                                           margin: 1rem 2.5%;
	                                           vertical-align: baseline;
	                                           position: relative; }
	    label { cursor: pointer; }
	    pre, pre code, p code, p pre code { width: 90%;
	               padding: 0; }
	    .table-caption { display: block;
	                     float: right;
	                     clear: both;
	                     width: 98%;
	                     margin-top: 1rem;
	                     margin-bottom: 0.5rem;
	                     margin-left: 1%;
	                     margin-right: 1%;
	                     vertical-align: baseline;
	                     position: relative; }
	    div.table-wrapper, table, table.booktabs { width: 85%; }
	    div.table-wrapper { border-right: 1px solid #efefef; }
	    img { max-width: 100%; }
	}
}
